:root {
  --primary-color: #222f65;
  --text-color: #ffffff;
}

.container {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  /* border: 2px solid purple; */
  /* margin: 5px; */
  /* margin-top: 100px; */
  /* margin-bottom: 100px; */

  padding: 0 25vw;
}

.tittleText {
  margin: 15px;
}

.tittle {
  text-align: center;
  margin: 1vw;

  font-size: 22px;
  text-transform: uppercase;
  background-color: #393e41;
  color: #ffffff;
  border-radius: 5px;
  /* border-bottom: 1px royalblue solid; */
  box-shadow: 5px 5px 10px rgba(32, 32, 32, 0.342),
    -5px -5px 10px rgb(255, 255, 255);
  /* font-weight: 800; */
}

.text {
  margin: 15px;
  color: var(--text-color);
}

.categoryItem {
  /* width: 100%; */
  border: 3px solid var(--primary-color);
  border-radius: 5px;
  text-align: center;
  font-size: 20px;
  /* font-weight: 800; */
  text-transform: uppercase;
  margin: 1vw;
  background-color: var(--primary-color);
}

.categoryItem:hover .text {
  color: var(--primary-color);
}

.categoryItem:hover {
  cursor: pointer;
  background-color: var(--text-color);
}

@media only screen and (max-device-width: 1024px) {
  .container {
    padding: 0;
  }
}
