:root {
  --bg-color: #e7ecff;
}
.container {
  border: 2px solid #929ecc;
  border-radius: 5px;
  /* margin-bottom: 400px; */
  background-color: var(--bg-color);
  margin: 100px 10px;
  padding: 5px;
  min-height: 400px;
}

@media only screen and (max-device-width: 1024px) {
  .container {
    margin: 50px 10px;
  }
}
