a {
  text-decoration: none;
  /* color: brown; */
}
.navBarContainer {
  height: 35vh;
  display: flex;
  border: 1px solid red;
  justify-content: space-between;
  align-items: baseline;
  background-color: #d5deff;
}

.navBarLogo {
  font-size: 24px;
}

.navBarWrapper {
  border: 1px solid blue;
  width: 70%;
}

.navBar {
  border: 1px solid black;
  font-size: 16px;
  /* width: 100%; */
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.hamburger {
  width: 25px;
  border: 3px solid orange;
  /* margin: auto; */
}

.hamburger > div {
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 3px;
}
